<template>
  <input
    type="file"
    rel="files"
    :multiple="multiple"
    @change="getFile"
    title="上传文件"
    ref="uploaddom"
    style="display: none"
  />
</template>

<script>
import axios from 'axios';
export default {
  // 这个名字很重要，它就是未来的标签名。
  name: 'BaseUpload',
  data() {
    return {
      fileList: [],
    };
  },
  props: {
    url: {
      type: String,
      required: true,
    },
    failed_redirectToUrl: String,
    axiosSettings: Object,
    extraParams: Object,
    exts: String,
    size: [String, Number],
    multiple: { type: Boolean, default: false },
    choose: { type: Function },
    done: { type: Function },
    genUploadPercent: { type: Function },
    // carmera:Boolean !== false
  },
  methods: {
    getCookie(name) {
      let arr,
        reg = new RegExp('(^| )' + name + '=([^;]*)(;|$)');
      if ((arr = document.cookie.match(reg))) return unescape(arr[2]);
      else return null;
    },
    getFile(e) {
      this.fileList = e.target.files;
      if (!this.fileList || this.fileList.length === 0) {
        return false;
      }
      let exts = this.exts.split('|');

      for (let i = 0; i < this.fileList.length; i++) {
        let item = this.fileList[i];
        let fileNameArr = item.name.split('.');
        let fileext = fileNameArr[fileNameArr.length - 1].toLowerCase();
        if (exts.indexOf(fileext) === -1) {
          this.Toast('上传文件的格式必须为：' + '\n' + this.exts);
          this.$refs.uploaddom.value = null;
          throw '上传了不符合后缀的文件！';
        }
        if (this.size) {
          let limitFileSize = this.size * 1024;
          if (item.size > limitFileSize) {
            this.Toast('上传文件超过限制大小！');
            this.$refs.uploaddom.value = null;
            throw '上传文件超过限制大小！';
          }
        }
      }
      if (this.choose) {
        this.choose(this.fileList);
      } else {
        this.$emit('choose', this.fileList);
      }
      let formData = new FormData();
      for (let i = 0; i < this.fileList.length; i++) {
        formData.append('file', this.fileList[i]);
      }
      // 如果有传入额外的参数
      if (this.extraParams) {
        for (let key in this.extraParams) {
          formData.append(key, this.extraParams[key]);
        }
      }
      let _this = this;
      let axiosSettings = {
        url: this.url,
        data: formData,
        method: 'POST',
        headers: { IDSTGC: this.getCookie('IDSTGC') },
      };
      if (this.axiosSettings && typeof this.axiosSettings === 'object') {
        for (let key in this.axiosSettings) {
          if (key !== 'url' && key !== 'data') {
            axiosSettings[key] = this.axiosSettings[key] || axiosSettings[key];
          }
        }
      }
      if (_this.genUploadPercent) {
        // onDownloadProgress 配置该属性代表允许为下载处理进度事件
        axiosSettings.onDownloadProgress = function (progressEvent) {
          //axios封装的原生获取下载进度的事件，该回调参数progressEvent中包含下载文件的总进度以及当前进度
          if (progressEvent.lengthComputable) {
            //属性lengthComputable主要表明总共需要完成的工作量和已经完成的工作是否可以被测量
            //如果lengthComputable为false，就获取不到progressEvent.total和progressEvent.loaded
            //实时获取最新下载进度
            _this.genUploadPercent((progressEvent.loaded / progressEvent.total) * 100);
          }
        };
      }
      axios(axiosSettings).then(data => {
        this.$refs.uploaddom.value = null;
        console.log('uploadData:', data);
        let res = data.data;
        if (res && (res.code === '000000' || res.success === true)) {
          this.done ? this.done(res) : this.$emit('done', res);
        } else {
          if (res && Object.prototype.hasOwnProperty.call(res, 'login') && !res.login) {
            let pathName = window.document.location.pathname;
            let projectName = pathName.substring(0, pathName.substr(1).indexOf('/') + 1);
            let url = projectName + '/';
            let purl = document.location.href; //
            location.href = this.failed_redirectToUrl
              ? this.failed_redirectToUrl
              : url + 'rest/Idsclient/reqLogin?reqUrl=' + encodeURIComponent(purl);
          } else {
            this.Toast(res.errInf.shortBusInf);
            this.done ? this.done(res) : this.$emit('done', res);
          }
        }
      });
    },
    click_upload() {
      this.$refs.uploaddom.click();
    },
    Toast(message) {
      let _div = document.createElement('div');
      let divCss = {
        position: 'fixed',
        top: '50%',
        width: '100%',
      };
      for (let i in divCss) {
        _div.style[i] = divCss[i];
      }
      let _message = document.createElement('sapn');
      _message.innerText = message;
      let spanCss = {
        'background-color': 'rgba(0,0,0,0.5)',
        padding: '15px 15px',
        'border-radius': '5px',
        color: '#FFF',
        'margin-left': 'auto',
        'margin-right': 'auto',
        'max-width': '500px',
        'max-height': '500px',
        'font-size': '1rem',
        display: 'block',
        'text-align': 'center',
      };
      for (let i in spanCss) {
        _message.style[i] = spanCss[i];
      }
      _div.appendChild(_message);
      let _body = document.getElementsByTagName('body')[0];
      _body.appendChild(_div);
      setTimeout(function () {
        _body.removeChild(_div);
      }, 3000);
    },
  },
  created() {
    this.exts = this.exts ? this.exts : 'jpg|png|jpeg|bmp|gif|txt|doc|xlsx|xls|ppt|rar|zip';
    // this.carmera = this.carmera ? 'image/*' : '';
  },
};
</script>